* {
    margin: 0;
    padding: 0;
}

@font-face {
	font-family: 'pixel';
	src: url(/assets/fonts/pixelsix10.ttf);
}

html,
body {
    overflow: hidden;
   
}
.wrapper{
    display: flex;
}
.webgl {
    position: fixed;
    top: 0;
    left: 20%;
    outline: none;
}

.loading-bar {
    position: absolute;
    top: 50%;
    width: 100%;
    height: 2px;
    background: #ffffff;
    transform: scaleX(0.3);
    transform-origin: top left;
    transition: transform 0.5s;
    z-index: 100;
}

.loading-bar.ended {
    transform: scaleX(0);
    transform-origin: 100% 0;
    transition: transform 1.5s ease-in-out;
}

.menu{
    position: relative;
    z-index:0;
    left:0;
    width: 20%;
    height:100vh;
    background-color: #000000;
    color: white;
    text-align: center;
    font-family: 'pixel';
}
.menu h3{
    font-size: 28px;
    width: 100%;
    padding-top: 108px;
    letter-spacing: 8px;
    padding-left:6px;
   
    
}
.bar{
    background-color: #fff;
    width:10%;
    height:3px;
    margin: 16px auto;
}
ul{
    margin:16px auto;
}
.ind{
    width: 8px;
    height:8px;
    background-color: aqua;
    vertical-align:baseline;
    display: none;
}
li{
    padding:24px 0;
    display:flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    font-size: 18px;
    cursor:pointer;
}
.grayout{
    color:#484848;
}
.menuBtns{
    width: 100px;
    margin: 40px auto;
}

.point {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 3;
}

.point:hover .text {
    opacity: 1;
    transition: opacity .3s;

}

.point.visible .label {
    transform: scale(1, 1);
}

.point .label {
    position: absolute;
    top: -20px;
    left: -20px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: white;
    background: #00000078;
    color: white;
    font-family: Helvatica, Arial, sans-serif;
    text-align: center;
    line-height: 40px;
    font-weight: 400;
    font-size: 14px;
    cursor: help;
    transform: scale(0, 0);
    transition: transform 0.3s;
}

.point .text {
    position: absolute;
    top: 30px;
    left: -120px;
    width: 200px;
    padding: 20px;
    border-radius: 4px;
    background: #00000078;
    color: white;
    text-align: center;
    line-height: 1.3em;
    font-family: Helvatica, Arial, sans-serif;
    font-weight: 600;
    font-size: 14px;
    opacity: 0;
    transition: opacity .3s;
    pointer-events: none;
}

.home {
    position: fixed;
    top: 16px;
    left: 16px;
    font-size: 20px;
    font-family: sans-serif;
    z-index: 3;
    color: white;
    cursor: pointer;
    border: 1px solid white;
    padding: 4px 8px;
    border-radius: 8px;
}

/* The Modal (background) */
.modal {
    display: none;
    /* Hidden by default */
    position: fixed;
    /* Stay in place */
    z-index: 100;
    /* Sit on top */
    left: 0;
    top: 0;
    width: 100%;
    /* Full width */
    height: 100%;
    /* Full height */
    overflow: auto;
    /* Enable scroll if needed */
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4);
    /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
    background-color: #fefefe;
    margin: 5% auto;
    /* 15% from the top and centered */
    border: 1px solid #888;
    width: 90%;
    /* Could be more or less, depending on screen size */
    border-radius: 16px;
    position: relative;
    padding: 42px;

}



/* The Close Button */
.close {
    color: white;
    background-color: #333333;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
    position: absolute;
    top: -20px;
    right: -20px;
    font-weight: bold;
    border-radius: 50%;
    transition: .2s ease-in-out;
    z-index: 3;
}

.close:hover,
.close:focus {
    transform: translate(-4px, -4px);
    cursor: pointer;
}

/* modal1 */

.para1 h3 {
    font-size: 72px;
    line-height: 1.4;

}

img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: .9;
    position: relative;
    border-radius: 14px;
}

/* modal2 */
.modal2 {
    padding: 0;
}

iframe {
    width: 100%;
    height: 800px;
    border-radius: 16px;
    border-width: 0;
    border-style: none;
}

body {
    font-family: "Helvetica Neue",
        Arial,
        "Hiragino Kaku Gothic ProN",
        "Hiragino Sans",
        Meiryo,
        sans-serif;
}

h3 {
    font-size: 34px;
    line-height: 2.2;
}

p {
    font-size: 16px;
}

@media screen and (max-width:480px) {
    .pc-only {
        display: none;
    }

    .sp-only {
        display: block;
    }

    .para1 h3 {
        font-size: 32px;
        line-height: 1.4;

    }

    .modal-content {
        width: 92%;
        padding: 8px;
    }
}